<template>
  <money
    ref="input"
    v-bind="money"
    v-model="value"
    @input.native="updateValue($event.target.value)"
  />
</template>

<script>
  import { Money } from 'v-money';

  export default {
    data () {
      return {
        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    props: ["value"],

    components: {
      Money,
    },

    methods: {
      updateValue (v) {
        var rawValue = v.replace(this.money.prefix, "");
        rawValue = rawValue.replace(this.money.thousands, "");

        this.$refs.input.value = v;
        this.$emit("input", parseFloat(rawValue));
      },
    },
  };
</script>



