<template>
  <div class="container">
    <div class="entry">
      <input
        ref="debitCategory"
        type="text"
        v-model="debit.category"
        placeholder="Category"
      />

      <db-money
        v-model="debit.amount"
        @keyup.native.enter="updateDebits"
      />

      <input
        class="memo"
        type="text"
        v-model="debit.memo"
        placeholder="Memo (optional)"
      />

      <button @click="updateDebits">{{ buttonTxt }}</button>
    </div>

    <ul>
      <li v-for="item in debits">
        <span>
          {{ item.category }}
          <small
            v-show="!!item.memo && !!item.memo.length"
          >
            ({{ item.memo }})
          </small>
        </span>

        <span>{{ item.amount | to-currency }}</span>

        <span class="actions">
          <icon
            name="edit"
            @click.native="editDebit(item)"
          />

          <icon
            class="icon--red"
            name="times"
            @click.native="removeDebit(item.id)"
          />
        </span>
      </li>

      <li v-for="item in bills">
        <span>
          {{ item.category }}
        </span>

        <span>{{ item.amount | to-currency }}</span>
      </li>
    </ul>

    <footer>
      <span>
        <strong>Remaining:</strong>
        {{ amountRemaning | to-currency }}
      </span>

      <span>
        <strong>Total:</strong>
        {{ amountSpent | to-currency }}
      </span>
    </footer>
  </div>
</template>

<script>
  import 'vue-awesome/icons';
  import moment from 'moment';

  import Settings from './utils/Settings';
  import Storage from './utils/Storage';

  import Debit from './Debit';
  import payDayBills from './PayDayBills';

  import Icon from 'vue-awesome/components/Icon';
  import dbMoney from './utils/Money';

  export default {
    data () {
      return {
        buttonTxt: "+ Add expense",

        debit: {
          amount: 0,
          category: "",
          memo: "",
        },

        debits: Debit.entries,

        bills: payDayBills.bills.filter(b => {
          let today = moment().startOf("day"),
              currentPayPeriod;

          if (today >= Settings.payDay2.startOf("day")) {
            currentPayPeriod = 2;
          } else {
            currentPayPeriod = 1;
          }

          return parseInt(b.payPeriod) == currentPayPeriod;
        }),
      };
    },

    components: {
      dbMoney,
      Icon,
    },

    computed: {
      ledgerItems () {
        return this.debits.concat(this.bills);
      },

      amountRemaning () {
        return Settings.income - this.amountSpent;
      },

      amountSpent () {
        return this.ledgerItems.reduce((acc, item) => {
          return acc += parseFloat(item.amount);
        }, 0);
      },
    },

    methods: {
      removeDebit (id) {
        Debit.remove(id);
      },

      editDebit (debit) {
        this.buttonTxt = "Save changes";
        this.debit = debit;
      },

      updateDebits () {
        if (!!!this.debit.id) {
          Debit.add({
            category: this.debit.category,
            amount: this.debit.amount,
            memo: this.debit.memo,
          });
        }

        this.debit = {
          category: "",
          amount: 0,
          memo: "",
        };
        console.log(this.debit.amount);

        this.$refs.debitCategory.focus();

        this.buttonTxt = "+ Add expense";
      },
    },

    mounted () {
      Settings.initiated();

      Debit.$on("updatedEntries", (entries) => {
        this.debits = entries;
      });
    },
  };
</script>

<style lang="scss" scoped>
  .container {
    display: grid;
    grid-template-columns: 1fr 300px;
    grid-template-rows: 1fr 40px;
    height: 100vh;
    width: 100vw;
  }

  .entry {
    grid-column-start: 2;
    grid-column-end: span 1;

    grid-row-start: 1;
    grid-row-end: span 2;

    border-left: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1rem;

    input {
      display: block;
      margin: 0 0 1rem;
      font-size: 1.2rem;
      padding: 0.5rem;
      width: 100%;

      &.memo {
        font-size: 1rem;
        padding: 0.25rem;
      }
    }
  }

  ul {
    grid-column-start: 1;
    grid-column-end: span 1;

    grid-row-start: 1;
    grid-row-end: span 1;

    max-height: 100%;
    overflow-y: auto;
    list-style: none;
    padding: 0;
    margin: 0;

    display: flex;
    flex-flow: column nowrap;

    li {
      min-height: 50px;
      padding: 1rem;
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
      margin: 0;
      display: flex;
      justify-content: space-between;
      transition: background-color 0.2s;
      position: relative;

      small {
        display: inline-block;
        margin-left: 1rem;
        color: rgba(0, 0, 0, 0.7);
      }

      .actions {
        position: absolute;
        right: 200px;
        top: 50%;
        transform: translateY(-50%);
        opacity: 0;
        transition: opacity 0.2s;
      }

      &:hover {
        background: rgba(0, 0, 0, 0.2);

        .actions {
          opacity: 1;
        }
      }
    }
  }

  footer {
    grid-column-start: 1;
    grid-column-end: span 1;

    grid-row-start: 2;
    grid-row-end: span 1;

    display: flex;
    justify-content: space-between;

    padding: 1rem;
  }

  .fa-icon {
    cursor: pointer;
    padding: 0 0.5rem;
  }

  .icon--red {
    color: red;
  }

  button {
    float: right;
    padding: 0.25rem 0.75rem;
    font-size: 0.9rem;
    color: rgba(109, 33, 148, 1);
    background: none;
    cursor: pointer;
    border: 3px solid rgba(109, 33, 148, 1);
    border-radius: 50vh;
    font-weight: bold;
    box-shadow: 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.2);
    transition: color 0.2s, background-color 0.2s;
    outline: 0;

    &::selection {
      background: none;
    }

    &:hover {
      color: white;
      background: darken(rgba(109, 33, 148, 1), 15%);
    }

    &:active {
      transform: translate(2px, 2px);
    }
  }
</style>



